.nav {
  &__list {
    display: flex;
    align-items: center;


    @media (max-width: 768px) {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 31px;


      @media (max-width: 768px) {
        margin-right: 0;
        margin-bottom: 40px;
      }
    }
  }
  &__link {
    padding-bottom: 6px;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: var(--black-color);
    transition: 0.2s ease-in-out;

    @media (max-width: 768px) {
      font-size: 26px;
    }
  }
  &__link:hover {
    border-bottom: 1px solid var(--accent-color);
    color: var(--accent-color);
  }
  &__link:active {
    color: var(--accent-color);
  }

  &__link:focus {
    padding: 2px;
    border: 1px solid var(--accent-color);
    outline: unset;
    color: var(--accent-color);
  }

  @include small-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--white-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

}

.menu--active {
  opacity: 1;
  visibility: visible;
}
