.started {
  background-color: var(--accent-color);

  &-wrap {
    display: flex;
    // align-items: center;
    justify-content: space-between;
    padding-top: 81px;
    padding-right: 100px;
    padding-left: 94px;
  }
  &-left {
    margin-bottom: 69px;
  }

  &-title {
    font-weight: 400;
    font-size: 32px;
    line-height: 45px;
    color: var(--black-color);
  }

  &-text {
    display: block;
    font-weight: 300;
    font-size: 32px;
    line-height: 45px;
    color: var(--black-color);
  }

  &__find-btn {
    padding: 14px 25px;
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    border: 1px solid var(--black-color);
    background-color: #231F20;
    border-radius: 5px;
    color: var(--white-color);
    transition: 0.2s ease-in-out;

    &:hover {
      filter: brightness(30%);
    }
    &:focus {
      filter: brightness(30%);
    }
    &:active {
      filter: brightness(30%);
    }
  }

  &__how-btn {
    margin-left: 16px;
    padding: 13px 25px;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
    border: 1px solid var(--black-color);
    border-radius: 5px;
    color: var(--black-color);
    transition: 0.2s ease-in-out;

    &:hover {
      background-color: rgba($color: #e07228b9, $alpha: .5);
      color: var(--white-color);
    }

    &:focus {
      outline: none;
      background-color: rgba($color: #e07228b9, $alpha: .5);
      color: var(--white-color);
    }
    &:active {
      outline: none;
      background-color: rgba($color: #e07228b9, $alpha: .5);
      color: var(--white-color);
    }

  }

  &-right__link {
    margin-top: 15px;
    display: inline-block;
  }

  @media (max-width: 992px) {

    &-wrap {
      flex-direction: column;
      align-items: center;
    }
    &-left {
      margin-bottom: 0px;
    }
    &-right__link {
      display: inline-block;
      margin-top: 10px;
      margin-bottom: 30px;
    }

  }

  @media (max-width: 600px) {
    &-wrap {
      padding-right: 0;
      padding-left: 0;
    }
    &-text, &-title {
      font-size: 25px;
      line-height: 32px;
      text-align: center;
    }

    &-right__link {
      display: block;
      margin-left: 0;
      margin-bottom: 15px;
    }
  }
}
