// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1330px;
  --content-main-width: 1170px;
  --content-fluid-width: 1440px;
  --content-footer-width: 1000px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-main-width: calc(var(--content-main-width) + (var(--container-offset) * 2));
  --container-fluid-width: calc(var(--content-fluid-width) + (var(--container-offset) * 2));
  --container-footer-width: calc(var(--content-footer-width) + (var(--container-offset) * 2));

  // colors
  --white-color: #fff;
  --accent-color: #F7941E;
  --black-color: #000000;

}
