.footer {
  height: 410px;
  background: #FAFAFA;
  padding-top: 115px;


  &-nav__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 53px;


  }

  &-nav__item {
    &:not(:last-child) {
      margin-right: 56px;
    }
  }
  &-nav__link {
    padding: 3px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--black-color);
    transition: 0.3s ease-in-out;
    outline: none;

    &:hover {
      color: var(--accent-color);
    }

    &:focus {
      outline: none;
      color: var(--accent-color);
      border: 1px solid var(--accent-color);
    }
    &:active {
      outline: none;
      color: var(--accent-color);
      border: 1px solid var(--accent-color);
    }
  }


  &-border {
    border-top: 1px solid var(--black-color);
  }

  &-privacy {
    margin-top: 62px;
    text-align: center;
  }

  &-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: var(--black-color);
  }
  &-terms {
    margin-left: 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: var(--black-color);
  }

  @media (max-width: 420px) {
    &-nav__item:not(:last-child) {
      margin-right: 30px;
    }
  }
}
