.blogs {
  padding-top: 88px;
  &-img {
    height: 100%;
  }
  &-title {
    margin-bottom: 24px;
    font-style: italic;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    color: var(--black-color)
  }
  &-descr {
    margin-bottom: 71px;
    font-style: italic;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: var(--black-color);
  }

  &__card-main {
    display: flex;

    &-descr {
      width: 66%;
      padding-top: 63px;
      padding-left: 78px;
      padding-bottom: 49px;
      background-color: #231F20;
    }
    &-name {
      font-style: italic;
      font-weight: 300;
      font-size: 14px;
      line-height: 20px;
      color: var(--white-color);
    }
    &-title {
      margin-top: 27px;
      margin-bottom: 60px;
      max-width: 65%;
      font-style: italic;
      font-weight: 400;
      font-size: 48px;
      line-height: 56px;
      color: var(--white-color);
    }
    &-more {
      font-style: italic;
      font-weight: 400;
      font-size: 18px;
      line-height: 20px;


    }
    &__link {
      transition: 0.2s ease-in-out;
      padding-bottom: 2px;
      color: var(--white-color);

      &:hover {
        border-bottom: 1px solid  #F7941E;
        color: rgba($color: #F7941E, $alpha: .9);
      }
      &:focus {
        outline: none;
        border: 1px solid var(--accent-color);
        color: rgba($color: #F7941E, $alpha: .9);
      }
      &:active {
        color: rgba($color: #F7941E, $alpha: .9);
      }
      @media (max-width: 500px) {
        font-size: 16px;
      }
    }
  }

  &__card {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    margin-bottom: 80px;

    &-left {
      display: flex;
      flex-direction: column;
      padding-top: 48px;
      padding-left: 48px;
      padding-bottom: 50px;
      max-width: 570px;
      height: 290px;
      transition: 0.3s ease-in-out;


      &-title {
        max-width: 65%;
        font-style: italic;
        font-weight: 500;
        font-size: 24px;
        line-height: 34px;
      }

      &-link {
        padding-bottom: 2px;
        transition: 0.2s ease-in-out;
        color: var(--black-color);
        &:hover {
          border-bottom: 1px solid var(--accent-color);
          color: var(--accent-color);
        }

        &:focus {
          border-bottom: 1px solid var(--accent-color);
          color: var(--accent-color);
        }

        &:active {
          border-bottom: 1px solid var(--accent-color);
          color: var(--accent-color);
        }
      }

      &-more {
        display: block;
        margin-top: auto;
        font-family: 'Gilroy';
        font-size: 14px;
        line-height: 140.62%;
      }
      &-descr {
        margin-top: 16px;
        font-style: italic;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
      }
    }

    &-left:hover {
      box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.15);
    }
    &-right {
      display: flex;
      flex-direction: column;
      padding-top: 48px;
      padding-left: 48px;
      padding-bottom: 50px;
      height: 290px;
      max-width: 570px;
      transition: 0.3s ease-in-out;


      &-title {
        max-width: 97%;
        font-style: italic;
        font-weight: 500;
        font-size: 24px;
        line-height: 34px;
      }

      &-link {
        padding-bottom: 2px;
        transition: 0.2s ease-in-out;
        color: var(--black-color);

        &:hover {
          border-bottom: 1px solid  #F7941E;
          color: rgba($color: #F7941E, $alpha: .9);
        }
        &:focus {
          outline: none;
          border: 1px solid var(--accent-color);
          color: rgba($color: #F7941E, $alpha: .9);
        }
        &:active {
          color: rgba($color: #F7941E, $alpha: .9);
        }
      }

      &-more {
        display: block;
        margin-top: auto;
        font-family: 'Gilroy';
        font-size: 14px;
        line-height: 140.62%;
      }
      &-descr {
        margin-top: 16px;
        font-style: italic;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
      }
    }

    &-right:hover {
      box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.15);
    }


  }
  @media (max-width: 992px) {

    &__card {
      align-items: center;
      flex-direction: column;
      row-gap: 30px;
    }
    &__card-left, &__card-right {
      padding-top: 20px;
    }

    &__card-main-descr {
      padding-top: 30px;
      padding-left: 30px;
      width: 100%;

    }
    &__card-main-title {
      max-width: 65%;
      font-size: 38px;
    }

  }
  @media (max-width: 820px) {

    &__card-main-descr {
      padding-left: 15px;
    }

    &__card-main-title {
      font-size: 32px;
    }
  }
  @media (max-width: 700px) {
    &__card-main {
      align-items: center;
      flex-direction: column;
      background: #231F20;
    }
    &__card-main-descr {
      display: flex;
      flex-direction: column;
      padding-left: 48px;
    }
  }

  @media (max-width: 420px) {

    &-title {
      font-size: 26px;
    }
    &-descr {
      font-size: 16px;
    }
    &__card-left-title, &__card-right-title  {
      font-size: 20px;
      line-height: 24px;
    }

    &__card-left-descr, &__card-right-descr {
      font-size: 18px;
    }
  }
}
