.burger {
  display: none;

  @include small-tablet {
    display: block;
    margin-left: auto;
    margin-right: 10px;
    position: relative;
    // z-index: 501;
    border: 1px solid var(--black-color);
    border-radius: 10px;
    width: 51px;
    height: 51px;

    &__line {
      position: absolute;
      top: 50%;
      left: 50%;
      border-radius: 100px;
      width: 26px;
      height: 2px;
      background-color: var(--black-color);
      transform: translateX(-50%) translateY(-50%);

      &:first-child {
        margin-top: -7px;
      }

      &:last-child {
        margin-top: 7px;
      }
    }
  }
}

@media (max-width: 768px) {
  .burger-close {
    position: absolute;
    top: 30px;
    right: var(--container-offset);
    border: 1px solid var(--black-color);
    border-radius: 10px;
    width: 51px;
    height: 51px;
    background-image: url("../../img/x.svg");
    background-position: center;
    background-size: 24px 24px;
    background-repeat: no-repeat;
  }
}
