.gifts {

  &-title {
    margin-top: 96px;
    font-weight: 400;
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    color: var(--black-color);
  }

  &-descr {
    margin-top: 24px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: var(--black-color);
  }

  &__list {
    margin-top: 64px;
    margin-bottom: 137px;
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    padding-bottom: 24px;
    background-color: #FAFAFA;
    transition: 0.2s ease-in-out;

    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__item:hover {
    filter: drop-shadow(10px 10px 15px rgba(0, 0, 0, 0.15));
  }

  &__item-title {
    display: block;
    margin-top: 24px;
    margin-bottom: 74px;
    padding-left: 24px;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    color: var(--black-color)
  }
  &__item-more {
    padding-left: 24px;
    display: block;
    margin-top: auto;
  }
  &__item-link {
    padding: 3px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    transition: 0.2s ease-in-out;
    color: var(--black-color);

    &:hover {
      color: var(--accent-color);
    }

    &:focus {
      outline: none;
      border: 1px solid var(--accent-color);
      color: var(--accent-color);
    }
  }

  @media (max-width: 992px) {
    &__item:not(:last-child) {
      margin-right: 10px;
    }
  }

  @media (max-width: 838px) {
    &__list {
      flex-wrap: wrap;
      justify-content: space-evenly;
      align-items: center;
      row-gap: 20px;
      margin-bottom: 50px;
    }
  }
  @media (max-width: 420px) {
    &-title {
      font-size: 24px;
    }
  }
}
