.about {


  background-color: #F1F1F2;

  &-img {
    height: 100%;
  }
  &__find {
    display: flex;
    // max-height: 700px;
  }
  &__find-left {
    padding-top: 154px;
    padding-left: 160px;
    flex-basis: 50%;
  }
  &__find-right {
    flex-basis: 50%;
  }

  &__find-title {
    margin-bottom: 45px;
    max-width: 80%;
    font-weight: 400;
    font-size: 56px;
    line-height: 66px;
    color: var(--black-color);
  }

  &__find-descr {
    margin-bottom: 52px;
    max-width: 75%;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--black-color);
  }

  &__find-btn {
    padding: 19px 25px;
    width: 200px;
    background-color: var(--accent-color);
    border-radius: 5px;
    border: 1px solid var(--accent-color);
    transition: 0.2s ease-in-out;

  }

  &__find-btn:hover {
    filter: brightness(120%)
  }
  &__find-btn:focus {
    filter: brightness(120%)
  }
  &__find-btn:active {
    filter: brightness(120%)
  }

  &__find-link {
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    color: var(--white-color);
  }



  &__work {
    // max-height: 600px;
    display: flex;
  }

  &__work-left {
    flex-basis: 50%;
  }
  &__work-right {
    padding-top: 164px;
    padding-left: 112px;
    flex-basis: 50%;
  }

  &__work-title {
    margin-bottom: 23px;
    font-weight: 400;
    font-size: 32px;
    line-height: 28px;
    color: var(--black-color);
  }
  &__work-descr {
    margin-bottom: 35px;
    max-width: 77%;
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    text-align: justify;
    color: var(--black-color);
  }

  &__work-btn {
    padding: 17px 30px;
    width: 200px;
    border: 1px solid var(--accent-color);
    border-radius: 5px;
  }

  &__work-link {
    padding: 17px 30px;
    margin-bottom: 30px;
    font-weight: 300;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
    border: 1px solid var(--accent-color);
    border-radius: 5px;
    color: var(--accent-color);
    transition: 0.2s ease-in-out;

    &:hover {
      background: var(--accent-color);
      color: var(--white-color);
    }
    &:focus {
      outline: none;
      background: var(--accent-color);
      color: var(--white-color);
    }
    &:focus {
      outline: none;
      background: var(--accent-color);
      color: var(--black-color);
    }
  }

  @media (max-width: 1200px) {
    &__find-left {
      padding-top: 50px;
      padding-left: 70px;
    }

    &__work-right {
      padding-top: 50px;
      // padding-left: 70px;
    }


    &__find-title {
      max-width: 70%;
      font-size: 46px;
    }
  }

  @media (max-width: 1024px) {
    &__find-left {
      padding-bottom: 30px;
    }
    &__find-title {
      margin-bottom: 20px;
    }
    &__find {
      flex-direction: column;
      align-items: center;
      justify-content: center;

      &-right {
        order: -1;
      }
    }

    &__work {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      &-right {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0;
      }
      &-btn {
        margin-bottom: 20px;
      }

    }
  }

  @media (max-width: 520px) {
    &__find-left {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 0;


    }
    &__find-title {
      max-width: 100%;
      font-size: 34px;
      line-height: 40px;
    }

    &__find-descr {
      max-width: 100%;
    }
  }
}
